.main_section{
  width: 100%;
  position: relative;
  width: 100%;
  height: rem(700px);
  overflow: hidden;
    img{

      position: absolute;
    }
  .main_title{
    font-family: $Proxima_Nova;
    font-size: rem(80px);
    font-weight: 400;
    color: #ffffff;
    text-align: center;
    position: absolute;
    top: 50%;
    left: 50%;
    -webkit-transform: translate(-50%);
            transform: translate(-50%)
  }
}



